"use client";
import React, { useState, useMemo } from 'react';
import BlogHero from '@/components/blog/BlogHero';
import FeaturedSection from '@/components/blog/FeaturedSection';
import BlogGrid from '@/components/blog/BlogGrid';
import { BLOG_POSTS } from '@/lib/blog-data';
import { Mail, ArrowRight, Zap, Trophy, Rocket } from 'lucide-react';
import Link from 'next/link';

export default function BlogPage() {
    const [activeCategory, setActiveCategory] = useState('All');
    const [searchTerm, setSearchTerm] = useState('');

    // Get featured posts (max 2 for the featured section)
    const featuredPosts = useMemo(() => {
        return BLOG_POSTS.filter(post => post.isFeatured).slice(0, 2);
    }, []);

    // Filter posts based on category and search term
    const filteredPosts = useMemo(() => {
        return BLOG_POSTS.filter(post => {
            const matchesCategory = activeCategory === 'All' || post.category === activeCategory;
            const matchesSearch = post.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                post.excerpt.toLowerCase().includes(searchTerm.toLowerCase());

            // Exclude featured posts if we are on the "All" category to avoid redundancy, 
            // but only if they are being displayed in the featured section
            const isActuallyFeatured = featuredPosts.some(fp => fp.id === post.id);

            return matchesCategory && matchesSearch;
        });
    }, [activeCategory, searchTerm, featuredPosts]);

    const displayPosts = useMemo(() => {
        // If we're on "All", we might want to hide featured posts from the grid
        if (activeCategory === 'All' && !searchTerm) {
            return filteredPosts.filter(post => !post.isFeatured);
        }
        return filteredPosts;
    }, [filteredPosts, activeCategory, searchTerm]);

    return (
        <main className="min-h-screen bg-background">
            <BlogHero
                activeCategory={activeCategory}
                onCategoryChange={setActiveCategory}
                onSearch={setSearchTerm}
            />

            {/* Featured Section - Only show on All category or when not searching */}
            {activeCategory === 'All' && !searchTerm && (
                <FeaturedSection posts={featuredPosts} />
            )}

            {/* Main Blog Grid */}
            <section className="py-12">
                <div className="max-w-7xl mx-auto px-4 md:px-6">
                    <div className="flex items-center gap-4 mb-12">
                        <h2 className="text-2xl md:text-3xl font-black tracking-tight">
                            {searchTerm ? `Results for "${searchTerm}"` : activeCategory === 'All' ? 'Latest Stories' : activeCategory}
                        </h2>
                        <div className="h-px bg-white/10 flex-grow" />
                        <span className="text-xs font-bold text-slate-500 uppercase tracking-widest">{displayPosts.length} Articles</span>
                    </div>

                    <BlogGrid posts={displayPosts} />
                </div>
            </section>

            {/* Newsletter Section */}
            <section className="py-24 relative overflow-hidden">
                <div className="absolute inset-0 bg-brand-medium/5 -z-10" />
                <div className="max-w-7xl mx-auto px-4 md:px-6 text-center">
                    <div className="max-w-3xl mx-auto bg-[#0b0f1a] border border-white/5 rounded-[3rem] p-12 md:p-20 relative">
                        <div className="absolute -top-10 left-1/2 -translate-x-1/2 w-20 h-20 bg-brand-cyan rounded-2xl rotate-12 flex items-center justify-center text-[#020617] shadow-xl shadow-brand-cyan/20">
                            <Mail className="w-10 h-10" />
                        </div>

                        <h2 className="text-3xl md:text-5xl font-black mb-6 tracking-tight">Get Growth Insights<br /><span className="text-brand-cyan">Delivered Weekly</span></h2>
                        <p className="text-slate-400 text-lg mb-10 leading-relaxed">
                            Join 5,000+ ambitious founders and marketers. We send one actionable email every Tuesday morning. No fluff, just growth.
                        </p>

                        <div className="flex flex-col sm:flex-row gap-4 max-w-lg mx-auto">
                            <input
                                type="email"
                                placeholder="your@email.com"
                                className="flex-grow h-16 px-8 rounded-2xl bg-white/5 border border-white/10 focus:border-brand-sky/40 outline-none transition-all text-lg"
                            />
                            <button className="h-16 px-10 bg-brand-cyan text-[#020617] rounded-2xl font-black uppercase tracking-widest hover:scale-105 transition-transform">
                                Join Now
                            </button>
                        </div>

                        <div className="mt-8 flex items-center justify-center gap-6 text-[10px] font-bold text-slate-500 uppercase tracking-widest">
                            <div className="flex items-center gap-2"><Zap className="w-3 h-3 text-brand-cyan" /> No Spam</div>
                            <div className="flex items-center gap-2"><Trophy className="w-3 h-3 text-brand-cyan" /> Exclusive Tips</div>
                            <div className="flex items-center gap-2"><Rocket className="w-3 h-3 text-brand-cyan" /> 1-Click Unsubscribe</div>
                        </div>
                    </div>
                </div>
            </section>

            {/* Final CTA Section */}
            <section className="py-24 border-t border-white/5">
                <div className="max-w-7xl mx-auto px-4 md:px-6">
                    <div className="bg-gradient-to-r from-brand-medium to-brand-deep rounded-[3rem] p-12 md:p-16 flex flex-col md:flex-row items-center justify-between gap-12 relative overflow-hidden group">
                        <div className="absolute top-0 right-0 w-96 h-96 bg-white/10 blur-[100px] rounded-full -translate-y-1/2 translate-x-1/2" />

                        <div className="relative z-10 max-w-xl text-center md:text-left">
                            <h2 className="text-4xl md:text-5xl font-black text-white mb-6 leading-tight">Ready to Turn Insights Into Action?</h2>
                            <p className="text-white/80 text-xl leading-relaxed">
                                Don't just read about growthâ€”experience it. Let our experts build and execute your winning digital strategy.
                            </p>
                        </div>

                        <div className="relative z-10 flex flex-col sm:flex-row gap-4 w-full md:w-auto">
                            <Link
                                href="/services"
                                className="px-8 py-5 bg-white text-brand-medium rounded-2xl font-black uppercase tracking-widest text-center transition-all hover:bg-slate-50 hover:shadow-xl hover:shadow-black/20"
                            >
                                Explore Our Services
                            </Link>
                            <Link
                                href="/contact"
                                className="px-8 py-5 bg-transparent border-2 border-white/20 text-white rounded-2xl font-black uppercase tracking-widest text-center transition-all hover:bg-white/10 flex items-center justify-center gap-2"
                            >
                                Book Free Consultation <ArrowRight className="w-5 h-5" />
                            </Link>
                        </div>
                    </div>
                </div>
            </section>
        </main>
    );
}
