import React from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { BlogPost } from '@/lib/blog-data';
import { Calendar, Clock, User, ArrowRight } from 'lucide-react';

interface FeaturedSectionProps {
    posts: BlogPost[];
}

export default function FeaturedSection({ posts }: FeaturedSectionProps) {
    if (posts.length === 0) return null;

    return (
        <section className="py-12">
            <div className="max-w-7xl mx-auto px-4 md:px-6">
                <div className="grid grid-cols-1 lg:grid-cols-12 gap-8">
                    {posts.map((post, idx) => (
                        <div
                            key={post.id}
                            className={`group relative overflow-hidden rounded-[2.5rem] bg-[#0b0f1a] border border-white/5 ${idx === 0 ? 'lg:col-span-8 h-[500px]' : 'lg:col-span-4 h-[500px]'
                                }`}
                        >
                            {/* Background Image Placeholder */}
                            <div className="absolute inset-0 bg-slate-900 z-0">
                                <div className="w-full h-full bg-slate-800 animate-pulse flex items-center justify-center text-slate-500 text-sm">
                                    Image: {post.featuredImage}
                                </div>
                            </div>

                            {/* Overlay */}
                            <div className="absolute inset-0 bg-gradient-to-t from-[#020617] via-[#020617]/40 to-transparent z-10" />

                            {/* Content */}
                            <div className="absolute inset-0 z-20 p-8 md:p-12 flex flex-col justify-end">
                                <div className="max-w-2xl">
                                    <div className="flex items-center gap-3 mb-6">
                                        <span className="px-4 py-1.5 bg-brand-cyan/20 backdrop-blur-md border border-brand-cyan/30 rounded-full text-xs font-bold text-brand-cyan uppercase tracking-widest">
                                            {post.category}
                                        </span>
                                        <span className="text-white/40 text-[10px] font-bold uppercase tracking-widest">Featured Story</span>
                                    </div>

                                    <Link href={`/blog/${post.slug}`}>
                                        <h2 className={`${idx === 0 ? 'text-3xl md:text-5xl' : 'text-2xl md:text-3xl'} font-black text-white mb-6 group-hover:text-brand-cyan transition-colors leading-tight`}>
                                            {post.title}
                                        </h2>
                                    </Link>

                                    <p className="text-slate-300 text-lg mb-8 line-clamp-2 max-w-xl leading-relaxed">
                                        {post.excerpt}
                                    </p>

                                    <div className="flex flex-wrap items-center gap-6 pt-8 border-t border-white/10">
                                        <div className="flex items-center gap-3">
                                            <div className="w-10 h-10 rounded-full bg-brand-medium/20 text-brand-cyan flex items-center justify-center text-xs font-black uppercase border border-brand-medium/30">
                                                {post.author.name.substring(0, 2)}
                                            </div>
                                            <div className="flex flex-col">
                                                <span className="text-white font-bold text-sm tracking-wide">{post.author.name}</span>
                                                <span className="text-white/40 text-[10px] uppercase font-bold tracking-widest">{post.author.role}</span>
                                            </div>
                                        </div>

                                        <div className="flex items-center gap-4 text-xs font-bold text-white/60 uppercase tracking-widest">
                                            <div className="flex items-center gap-2">
                                                <Calendar className="w-4 h-4 text-brand-cyan" />
                                                <span>{post.date}</span>
                                            </div>
                                            <div className="flex items-center gap-2">
                                                <Clock className="w-4 h-4 text-brand-cyan" />
                                                <span>{post.readTime}</span>
                                            </div>
                                        </div>

                                        <Link
                                            href={`/blog/${post.slug}`}
                                            className="ml-auto w-12 h-12 rounded-full bg-brand-cyan flex items-center justify-center text-[#020617] hover:scale-110 transition-transform hidden md:flex"
                                        >
                                            <ArrowRight className="w-6 h-6" />
                                        </Link>
                                    </div>
                                </div>
                            </div>

                            {/* Read More Link for mobile */}
                            <Link href={`/blog/${post.slug}`} className="absolute inset-0 z-30 opacity-0 cursor-pointer" />
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
}
